import React, { Suspense, useState } from "react";
import "./App.css";
import { HomePage, shoelist, Nav } from "./tools/data";
import { Routes, Route, Outlet } from "react-router-dom";

// Lazy 로딩 설정
const Detail = React.lazy(() => import('./tools/data').then(module => ({ default: module.Detail })));
const E404Page = React.lazy(() => import('./tools/data').then(module => ({ default: module.E404Page })));
const Cart = React.lazy(() => import('./tools/data').then(module => ({ default: module.Cart })));

function App() {
  let [shoes, _shoes] = useState(shoelist);
  return (
    <>
        <Nav />
      <Suspense fallback={<><div className="shadow df-box fit100">로딩중...</div></>}>
        <Routes>
          <Route path="/" element={<HomePage shoes={[shoes, _shoes]} />} />
          <Route
            path="/detail/"
            element={
              <>
                <E404Page /> 
                <Outlet />
              </>
            }
          />
          <Route path="/detail/:id" element={<Detail shoes={shoes} />} />
          <Route path="/cart" element={<Cart />} />
          <Route path="*" element={<E404Page />} />
        </Routes>
      </Suspense>
    </>
  );
}

export default App;