import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams, useNavigate } from "react-router-dom";
import { addCount, addItem, minuscount } from "./../store";

let shoelist = [
  {
    id: 1,
    title: "Red Knit",
    content: "Born in Seoul",
    price: 110000,
    img: "https://codingapple1.github.io/shop/shoes2.jpg",
  },
  {
    id: 0,
    title: "White and Black",
    content: "Born in France",
    price: 120000,
    img: "https://codingapple1.github.io/shop/shoes1.jpg",
  },
  {
    id: 2,
    title: "Grey Yordan",
    content: "Born in the States",
    price: 130000,
    img: "https://codingapple1.github.io/shop/shoes3.jpg",
  },
];

const E404Page = () => {
  return (
    <>
      <div className="df-box shadow shadow-move fit100">
        <h1>없는 페이지입니다.</h1>
      </div>
    </>
  );
};

const AlertBox = (pp)=>{
  let [control, _control] = pp.control
  return (
    <>
      {control && <div className="df-box shadow shadow-move fit100 alert" onClick={()=>{_control(false)}}>{pp.content}</div>}
    </>
  )
}

const ShoeItem = (pp) => {
  let shoe = pp.shoe;
  const navigate = useNavigate();
  return (
    <div
      className="shoeitem move"
      key={shoe.id}
      onClick={() => {
        navigate(`/detail/${shoe.id}`);
      }}
    >
      <img src={shoe.img} alt="신발사진" />
      <h4>{shoe.title}</h4>
      <p>{shoe.content}</p>
      <p>{shoe.price}₩</p>
    </div>
  );
};

const Nav = () => {
  const navigate = useNavigate();
  return (
    <div>
      <div
        className="nav nav-back shadow shadow-move"
        onClick={() => {
          navigate(-1);
        }}
      >
        ←
      </div>
      <div className="nav shadow shadow-move">
        <div className="nav-brand">
          <h2
            onClick={() => {
              navigate("/");
            }}
          >
            신발파는곳
          </h2>
        </div>
        <div className="nav-link">
          <p
            onClick={() => {
              navigate("/");
              alert(
                "상품목록에서 그림을 클릭하시면 상세정보를 보실수 있습니다."
              );
            }}
          >
            상세정보
          </p>
          <p
            onClick={() => {
              navigate("/cart");
            }}
          >
            장바구니
          </p>
        </div>
      </div>
    </div>
  );
};

const sortshoe = (shoes, _shoes) => {
  let tp = [...shoes];
  tp.sort((a, b) => {
    return a.title.localeCompare(b.title);
  });
  _shoes(tp);
}

const HomePage = (pp) => {
  let [shoes, _shoes] = pp.shoes;
  return (
    <>
      <div className="main-bg df-box shadow"></div>
      <h3 className="tac m20">상품목록</h3>
      <div className="df-box shadow shadow-move fit100 shoelist-menu">
        <span
          className="menu"
          onClick={() => {sortshoe(shoes, _shoes)}}>
          가나다순 정렬
        </span>
      </div>
      <div className="df-box shadow fit100 shoelist ">
        {shoes.map((a, i) => {
          return <ShoeItem shoe={a} i={i} key={i}></ShoeItem>;
        })}
      </div>
    </>
  );
};

const Detail = (pp) => {
  let [ani, _ani] = useState("");
  let dispatch = useDispatch();
  let [alertcontrol,_alertcontrol] = useState(false)
  useEffect(() => {
    _ani("detail-img-ani");

    return ()=>{
      _ani("");
    }
  }, []);

  let { id } = useParams();
  let shoe = pp.shoes.find((x) => {
    return x.id === parseInt(id);
  });
  if (!shoe) {
    return <E404Page></E404Page>;
  }
  return (
    <>
      <div className="df-box shadow fit100 detail-card ">
        <img src={shoe.img} alt="" className={`detail-img ${ani}`} />
        <h3>{shoe.title}</h3>
        <p>{shoe.content}</p>
        <p>{shoe.price}</p>
        <div
          className="df-box shadow shadow-move btn"
          onClick={() => {
            dispatch(addItem({ id: shoe.id, name: shoe.title, count: 1 })); _alertcontrol(true);
          }}
        >
          장바구니에 추가
        </div>
        <AlertBox content="장바구니에 추가됨" control={[alertcontrol,_alertcontrol]}/>
      </div>
    </>
  );
};

const Cart = () => {
  let cart = useSelector((state) => state.cart);
  let dispatch = useDispatch();
  return (
    <>
      <table className="shadow df-box cart shadow-move">
        <thead>
          <tr>
            <th className="short-table">#</th>
            <th className="long-table">상품명</th>
            <th className="short-table">수량</th>
            <th className="short-table">변경하기</th>
          </tr>
        </thead>
        <tbody>
          {cart.map((a, i) => {
            return (
              <tr key={i}>
                <td>{a.id}</td>
                <td>{a.name}</td>
                <td>{a.count}</td>
                <td>
                  <span className="control-btn shadow shadow-move"
                    onClick={() => {
                      dispatch(addCount(a.id));
                    }}
                  >
                    +
                  </span>
                  <span  className="control-btn shadow shadow-move"
                    onClick={() => {
                      dispatch(minuscount(a.id));
                    }}
                  >
                    -
                  </span>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <h1 className="fit100 df-box shadow">구매못함</h1>
    </>
  );
};

export { shoelist, ShoeItem, Nav, HomePage, Detail, E404Page, Cart, AlertBox };
